body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: black;
  /* display: flex; */
}


html, body {
  width: 100%;
  height: 100%;
}

.banner {
  /* position: relative; */
  /* width: 100%; */
  /* min-height: 100; */
  background: url('http://tinyurl.com/y3zzjsde');
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  -webkit-filter: saturate(1.5);
          filter: saturate(1.5);
}

.banner:after {
  content: '';
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: -webkit-gradient(linear,left top, right bottom,from(#c7825200),to(#ff0047));
  background-image: -webkit-linear-gradient(top left,#c7825200,#ff0047);
  background-image: linear-gradient(to bottom right,#c7825200,#ff0047);
  opacity: 0.6;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* NAVBAR FLEXBOX */
/* @import "compass/css3"; */

.navigation {
  list-style: none;
  margin: 0;

  /* background: deepskyblue; */
  background: black;
  display: flex;

  flex-flow: row;
  justify-content: flex-end;
}

.navigation a {
  text-decoration: none;
  display: block;
  padding: 1em;
  color: white;
}

.navigation a:hover {
  background: darken(deepskyblue, 2%);
}

@media all and (max-width: 800px) {
  .navigation {
    justify-content: space-around;
  }
}

@media all and (max-width: 600px) {
  .navigation {
    flex-flow: column wrap;
    padding: 0;
  }

  .navigation a {
    text-align: center;
    padding: 10px;
    border-top: 1px solid rgba(255,255,255,0.3);
    border-bottom: 1px solid rgba(0,0,0,0.1);
  }

  .navigation li:last-of-type a {
    border-bottom: none;
  }
}

